<template>
    <div id="Calendar_date" class="Parents_div">
        <div class="arrow" id="arrowleft" @click="scrollLeft"><font-awesome-icon :icon="['fas', 'circle-left']" /></div>
        <div><input type="date"></div>
        <div class="arrow" id="arrowright" @click="scrollRight"><font-awesome-icon :icon="['fas', 'circle-right']" /></div>
    </div>
    <div id="Calendar_container">
        <p id="Calendar_error">이번달 학사일정을 알아보세요!</p>
    </div>
</template>


<script>
export default {
    methods: {
        scrollLeft() {
            const container = document.getElementById('Calendar_container');
            container.scrollLeft -= container.offsetWidth;
        },
        scrollRight() {
            const container = document.getElementById('Calendar_container');
            container.scrollLeft += container.offsetWidth;
        }
    }
};
</script>


<style>

#school-Calendar {
    flex-shrink: 0;
    transition: 1s;
    width: 300px;
    height: 450px;

}

#Calendar_container {
    margin-top: 90px;
    width: 100%;
    height: 350px;
    display: flex;
    overflow-x: scroll;
    scroll-snap-type: x mandatory;
    transition: 1s;
    scroll-behavior: smooth;

    text-align: center;
    font-size: 20px;
}

#Calendar_error {
    width: 100%;
    text-align: center;
}

.Calendar-item {
    width: 100%;
    height: 300px;
    flex-shrink: 0;
    scroll-snap-align: start;
}

#school-Calendars_Timetable {
    width: 1200px;
    height: 450px;
    justify-content: space-between;
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* ❣️❣️ 급식 부분 > 날짜 지정 ❣️❣️ */

#Calendar_date {
    width: 250px;
    height: 20px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 20px;
    padding: 10px;

    position: absolute;
    top: 10px;
    left: 50%;
    transform: translate(-50%);

    box-shadow: none;

    transition: .4s;
}

#Calendar_date:hover {
    box-shadow: 0 0 8px 2px #2135551d;
}


#Calendar_select_container input::-webkit-calendar-picker-indicator:hover {
    cursor: pointer;
}


.arrow {
    cursor: pointer;
    transition: .2s;
}

.arrow:hover {
    transform: scale(1.1);
}
</style>