<template>
  <div class="Circle-graph Parents_div" ref="chartContainer"></div>
</template>

<script>
import { onMounted, ref, watch } from 'vue';
import * as echarts from 'echarts';
import { EventBus } from '../event';

export default {
  setup() {
    const chartContainer = ref(null);
    let chart;

    function createChart(studentGenderData) {
      if (!chartContainer.value) {
        return;
      }

      if (!chart) {
        chart = echarts.init(chartContainer.value);
      }

      const filteredData = studentGenderData.filter(item => item.value !== 0);

      const option = {
        grid: {
          top: '80%',
        },
        title: {},
        legend: {
          data: ['남자', '여자'],
          top: 'bottom',
        },
        series: [
          {
            name: '성별 데이터',
            type: 'pie',
            radius: '80%',
            data: filteredData,
            center: ['50%', '45%'],
            label: {
              position: 'inside',
              formatter: function(params) {
                if (filteredData.length === 1) {
                  return `{a|${params.value}명}\n{b|${params.percent}%}`;
                } else {
                  return `{a|${params.value}명}\n({b|${params.percent}%})`;
                }
              },
              rich: {
                a: {
                  fontSize: filteredData.length === 1 ? 19 : 12,
                  lineHeight: 24,
                  fontFamily: 'SUITE-Regular',
                },
                b: {
                  fontSize: 15,
                  lineHeight: 24,
                  fontFamily: 'SUITE-Regular',
                },
              },
            },
            labelLine: {
              show: false,
            },
            emphasis: {
              scale: 0,
            },
          },
        ],
      };

      chart.setOption(option);
    }

    watch(EventBus.studentGender, (newData) => {
      createChart(newData);
    });

    onMounted(() => {
      createChart(EventBus.studentGender.value);
    });

    return { chartContainer };
  },
};
</script>
