<template>
    <div id="Meal_select_container" class="Parents_div">
        <div class="arrow" id="arrowleft" @click="scrollLeft"><font-awesome-icon :icon="['fas', 'circle-left']" /></div>
        <div><input type="date"></div>
        <div class="arrow" id="arrowright" @click="scrollRight"><font-awesome-icon :icon="['fas', 'circle-right']" /></div>
    </div>
    <div id="Meal_container">
        <p id="Meal_error">오늘의 급식을 알아보세요!</p>
    </div>
</template>

<script>
export default {
    name: "date_Caledar",
    methods: {
        scrollLeft() {
            const container = document.getElementById('Meal_container');
            container.scrollLeft -= container.offsetWidth;
        },
        scrollRight() {
            const container = document.getElementById('Meal_container');
            container.scrollLeft += container.offsetWidth;
        }
    }
};
</script>


<style>
#school-Meals {
    flex-shrink: 0;
    transition: 1s;
    width: 300px;
    height: 450px;

}

#Meal_container {
    margin-top: 90px;
    width: 100%;
    height: 350px;
    display: flex;
    overflow-x: scroll;
    scroll-snap-type: x mandatory;
    transition: 1s;
    scroll-behavior: smooth;

    text-align: center;
    font-size: 20px;
}

#Meal_error {
    width: 100%;
    text-align: center;
}

.meal-item {
    width: 100%;
    height: 300px;
    flex-shrink: 0;
    scroll-snap-align: start;
}

#school-Meals_Timetable {
    width: 1200px;
    height: 450px;
    justify-content: space-between;
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* ❣️❣️ 급식 부분 > 날짜 지정 ❣️❣️ */

#Meal_select_container {
    width: 250px;
    height: 20px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 20px;
    padding: 10px;

    position: absolute;
    top: 10px;
    left: 50%;
    transform: translate(-50%);

    box-shadow: none;

    transition: .4s;
}

#Meal_select_container:hover {
    box-shadow: 0 0 8px 2px #2135551d;
}

#Meal_select_container input {
    font-size: 20px;
    border: 0;
    background-color: inherit;
    padding-left: 20px;
    cursor: pointer;
}

#Meal_select_container input::-webkit-calendar-picker-indicator:hover {
    cursor: pointer;
}


.arrow {
    cursor: pointer;
    transition: .2s;
}

.arrow:hover {
    transform: scale(1.1);
}
</style>