import { ref } from 'vue';

const EventBus = {


    chartData: ref([]),
    location: ref({ longitude: 126.9780, latitude: 37.5665 }),
    studentNumber: ref(0),
    studentGender: ref([]),
    selectedRegion: ref(''), // Add this line to initialize selectedRegion
    averageStudentNumber: ref(0), // Add this line to initialize averageStudentNumber
    studentsBelowAverage: ref(0),
    selectedSchoolLink: ref(''),
    lastyearSUM: ref(0),
    SUM: ref(0),
    youtube: ref(''),





    updatelocation(longitude, latitude) {
        this.location.value = { longitude, latitude };
    },

    updateChartData(data) {
        this.chartData.value = data;
    },

    updateStudentnumber(studentNumber) {
        this.studentNumber.value = studentNumber;
    },

    updateStudentgender(studentGender) {
        this.studentGender.value = studentGender;
    },
    updateSelectedRegionAndAverage(selectedRegion, averageStudentNumber, studentsBelowAverage) {
        this.selectedRegion.value = selectedRegion;
        this.averageStudentNumber.value = averageStudentNumber;
        this.studentsBelowAverage.value = studentsBelowAverage;
    },

    updateSelectedSchoolLink(link) {
        this.selectedSchoolLink.value = link;
    },


    updateLastyearSUMs(lastyearSUM, SUM) {
        this.lastyearSUM.value = lastyearSUM;
        this.SUM.value = SUM;
    },


    updateAi(SUM2020, SUM2021, SUM2022, SUM2023, SUM2024) {
        const aiData = [SUM2020, SUM2021, SUM2022, SUM2023, SUM2024];
        this.updateChartData(aiData);
    },
    



};

export { EventBus };
