<template>
    <div id="Timetable_select_container" class="Parents_div">
        <div class="arrow" id="arrowleft" @click="scrollLeft"><font-awesome-icon :icon="['fas', 'circle-left']" /></div>
        <select name="" id="Timetable_select">
            <option value="" disabled selected hidden>학교를 선택해주세요!</option>
            <optgroup value="1학년" label="1학년">
            </optgroup>

            <optgroup value="2학년" label="2학년">
            </optgroup>

            <optgroup value="3학년" label="3학년">
            </optgroup>
        </select>
        <div class="arrow" id="arrowright" @click="scrollRight"><font-awesome-icon :icon="['fas', 'circle-right']" /></div>
    </div>

    <div id="timetable_container"></div>
</template>


<script>
export default {
    methods: {
        scrollLeft() {
            const container = document.getElementById('Meal_container');
            container.scrollLeft -= container.offsetWidth;
        },
        scrollRight() {
            const container = document.getElementById('Meal_container');
            container.scrollLeft += container.offsetWidth;
        }
    }
};
</script>


<style>
#school-Timetable {
    width: 300px;
}


#Timetable_select_container {
    width: 250px;
