<template>
  <div>
    <div id="map_container" style="background: none; box-shadow: none;">
      <div id="map_default" :style="{ zIndex: mapView ? 6 : 4 }"></div>
      <div id="map_roadview" :style="{ zIndex: mapView ? 4 : 6 }"
        style="font-size: 30px; text-align: center; line-height: 450px;">좌측 사이드바에서 학교를 선택해주세요!</div>
    </div>
    <div id="map_bt1" class="map_bt" :style="bt1Style" @click="toggleView">{{ mapView ? '로드뷰' : '지도' }}</div>
    <!-- <div id="map_bt2" class="map_bt" :style="bt2Style">길찾기</div> -->
  </div>
</template>

<script>
/* global kakao */
// @mouseout="handleMapContainerLeave"

import { onMounted, ref, watch } from 'vue';
import { EventBus } from './event';

export default {

  name: "KakaoMap",
  setup() {
    const mapView = ref(true);
    const map = ref(null);
    let marker = null;
    let roadview = null; // 로드뷰 객체 추가

    onMounted(() => {
      loadKakaoMapScript();
    });

    watch(() => EventBus.location.value, (newCoordinates) => {
      if (map.value) {
        if (mapView.value) {
          panTo(newCoordinates.latitude, newCoordinates.longitude);
        }
      }
    });


    function loadKakaoMapScript() {
      const script = document.createElement("script");
      script.onload = initMap; // 로드 완료 후 initMap 호출
      script.src =
        "https://dapi.kakao.com/v2/maps/sdk.js?appkey=505b0532b7a9b91b20ec62b2481b8038&autoload=false"; // YOUR_APP_KEY를 실제 앱 키로 변경
      document.head.appendChild(script);
    }

    function initMap() {
      kakao.maps.load(() => {
        const container = document.getElementById("map_default");
        const options = {
          center: new kakao.maps.LatLng(37.5665, 126.9780),
          level: 4,
        };
        map.value = new kakao.maps.Map(container, options);

        // 로드뷰 초기화
        roadview = new kakao.maps.Roadview(document.getElementById("map_roadview"));

        const roadviewClient = new kakao.maps.RoadviewClient();

        // watch를 통해 EventBus의 좌표 변경을 감지하고 로드뷰 업데이트
        watch(() => EventBus.location.value, (newCoordinates) => {
          if (!mapView.value) { // 로드뷰 모드에서만 업데이트
            const position = new kakao.maps.LatLng(newCoordinates.latitude, newCoordinates.longitude);
            roadview.setViewpoint({
              pan: position.getLng(),
              tilt: 0,
              zoom: -10,
            });

            roadviewClient.getNearestPanoId(position, 300, function (panoId) {
              if (panoId) {
                roadview.setPanoId(panoId, position);
              } else {
                // console.log("엄");
              }
            });
          }
        });
      });
    }


    function toggleView() {
      mapView.value = !mapView.value;
    }

    function panTo(latitude, longitude, level = 4) {
      const moveLatLon = new kakao.maps.LatLng(latitude, longitude);
      map.value.panTo(moveLatLon);
      map.value.setLevel(level);
      if (marker) {
        marker.setPosition(moveLatLon);
      } else {
        marker = new kakao.maps.Marker({
          position: moveLatLon,
        });
        marker.setMap(map.value);
      }
    }

    return {
      mapView,
      toggleView,
    };
  },

  data() {
    return {
      bt1Style: {},
      bt2Style: {},
    };
  },

};
</script>








<style>
#map_container {
  width: 750px;
  height: 450px;
  position: relative;
  z-index: 2;
  background: none;
  border-radius: 20px;
}

#map_default,
#map_roadview {
  width: inherit;
  height: inherit;
  border-radius: inherit;
  position: absolute;
  box-shadow: 0 0 8px 2px #2135551d;
  bottom: 0;
  z-index: 4;

}



.map_bt {
  text-align: center;
  width: 70px;
  height: 100px;
  line-height: 40px;
  border-radius: 20px;
  background-color: #F5EFE7;

  position: absolute;
  top: 15px;
  left: 120px;

  box-shadow: inherit;
  transition: .2s;
  cursor: pointer;
  box-shadow: 0 0 8px 2px #2135551d;
  z-index: 1;
}

.map_bt:hover {
  transform: translateY(-5px);
}

#map_bt1 {
  margin-left: 15px;
}

#map_bt2 {
  margin-left: 95px;
}
</style>


