<template>
    <div class="Circle-graph Parents_div" ref="chartContainer" style="position: relative;">
        <div id="Number">
            <p ref="countingElement" class="inline">{{ animatedStudentNumber }}</p>&nbsp;명
        </div>
        <div id="Str">
            {{ selectedRegion }} 평균 학생 수보다<br>
            <span
                :class="{ 'lower': studentNumber < averageStudentNumber, 'higher': studentNumber > averageStudentNumber }"
                :style="{ color: studentNumber < averageStudentNumber ? '#85B8F8' : '#F1BFC9' }"
                class="Str_sub">
                {{ Math.abs(studentNumber - averageStudentNumber) }}명
                {{ studentNumber < averageStudentNumber ? '적어요 ⬇' : '많아요 ⬆' }} </span>
        </div>
    </div>
</template>
    
<script>
import { ref, watch } from 'vue';
import { EventBus } from '../event';

export default {
    setup() {
        const studentNumber = ref(0);
        const countingElement = ref(null);
        const selectedRegion = ref('');
        const averageStudentNumber = ref(0);

        watch(EventBus.studentNumber, (newStudentNumber) => {
            animateNumber(countingElement.value, studentNumber.value, newStudentNumber, 1000);
            studentNumber.value = newStudentNumber;
        });

        watch(EventBus.selectedRegion, (newRegion) => {
            selectedRegion.value = newRegion.replace('교육청','');
        });

        watch(EventBus.averageStudentNumber, (newAverage) => {
            averageStudentNumber.value = newAverage;
        });

        function animateNumber(element, startNumber, 끝번호, duration) {
            const range = 끝번호 - startNumber;
            const Inc = range / (duration / 16);
            let 현재번호 = startNumber;

            const updateNumber = () => {
                현재번호 += Inc;
                element.textContent = Math.floor(현재번호);

                if ((Inc > 0 && 현재번호 < 끝번호) || (Inc < 0 && 현재번호 > 끝번호)) {
                    requestAnimationFrame(updateNumber);
                } else {
                    element.textContent = 끝번호;
                }
            };

            updateNumber();
        }

        return {
            studentNumber,
            countingElement,
            selectedRegion,
            averageStudentNumber,
        };
    },
};
</script>



<style>
#Number {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -40%);
    font-size: 30px;

}

#Number>p {
    display: inline;
}

#Str {
    white-space: nowrap;
    text-align: center;
    position: absolute;
    top: 80%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.Str_sub {
    font-size: 23px;
    transition: 0.2s;
}
</style>
  

  
