<template>
    <div>
        <div ref="myChart" style="width: 790px; height: 400px;"></div>
    </div>
</template>
  
<script>
import * as echarts from 'echarts';
import { ref, onMounted, watch } from 'vue';
import { EventBus } from '../event';

export default {
    setup() {
        const myChart = ref(null);

        onMounted(() => {
            initChart();
        });

        const initChart = () => {
            const chart = echarts.init(myChart.value);

            const data = {

                xAxis: {
                    type: 'category',
                    data: ['2020년', '2021년', '2022년', '2023년', '2024년']
                },
                yAxis: {
                    type: 'value'
                },
                series: [{
                    data: [],
                    type: 'line',
                    label: {
                        show: true,
                        position: 'top'
                    },
                    areaStyle: {    // Add areaStyle to customize background
                        color: 'rgba(0, 128, 255, 0.3)'    // Set the background color and opacity
                    }
                }]
            };

            chart.setOption(data);

            watch(EventBus.chartData, newChartData => {
                chart.setOption({
                    series: [{
                        data: newChartData,
                        type: 'line',
                        label: {
                            show: true,
                            position: 'top',
                            formatter: '{c}' // Display the data value
                        },
                        areaStyle: {    // Add areaStyle to customize background
                            color: 'rgba(0, 128, 255, 0.3)'    // Set the background color and opacity
                        }
                    }]
                });
            });
        };

        return {
            myChart
        };
    }
};
</script>

