<template>
    <div class="Parents_div" style="position: relative; height: 100px; line-height: 100px; text-align: center;">
        <p v-if="differenceMessage !== '작년과 같아요'" style="position: absolute; left: 50%; top: 50%; transform: translate(-50%, -60%);">작년 학생수보다</p>
        <p :style="textStyle" style="position: absolute; width: 100%; white-space: nowrap; left: 50%; top: 50%; transition: 0.2s; font-size: 23px;">{{ differenceMessage }}</p>
    </div>
</template>

<script>
import { ref, watch, computed } from 'vue';
import { EventBus } from '../event';



export default {
    setup() {
        const LastyearSUM = ref(0);
        const SUM = ref(0);

        watch(EventBus.lastyearSUM, (newLastyearSUM) => {
            LastyearSUM.value = newLastyearSUM;
        });

        watch(EventBus.SUM, (newSUM) => {
            SUM.value = newSUM;
        });

        // 계산
        const differenceMessage = computed(() => {
            const difference = SUM.value - LastyearSUM.value;

            if (difference > 0) {
                return `${difference}명 많아요 ⬆`;
            } else if (difference < 0) {
                return `${Math.abs(difference)}명 적어요 ⬇`;
            } else {
                return '작년과 같아요';
            }
        });

        const textStyle = computed(() => {
            const translateY = differenceMessage.value === '작년과 같아요' ? '-50%' : '-30%';
            return {
                transform: `translate(-50%, ${translateY})`,
                color: textcolor.value
            };
        });

        const textcolor = computed(() => {
            if (SUM.value > LastyearSUM.value) {
                return "#F1BFC9";
            }
            if (SUM.value < LastyearSUM.value) {
                return "#85B8F8";
            } else {
                return '';
            }
        });

        return {
            LastyearSUM,
            SUM,
            differenceMessage,
            textStyle
        };
    },
};
</script>

